import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import { Popover } from 'antd'
import { StyledPreComponent } from './styles'

class PreComponent extends PureComponent {

    static propTypes = {
        type: PropTypes.oneOf(['pc', 'mobile']), // 
        name: PropTypes.string, // 
        desc: PropTypes.string, // 
        img: PropTypes.string, // 
        moreUrl: PropTypes.string, // 
        isActive: PropTypes.bool, // 
        onChange: PropTypes.func // 
    }

    handleClick = () => {
        const { type, name, isActive, onChange } = this.props
        onChange(type, name, isActive)
    }

    jumpToMore = (e) => {
        const { url } = e.currentTarget.dataset
        window.open(url)
    }

    render() {
        const { type, name, desc, img, moreUrl, isActive } = this.props
        return (
            <StyledPreComponent>
                <Popover
                    placement="left"
                    align={{ offset: [0, 0] }}
                    autoAdjustOverflow
                    overlayClassName="noPaddingPopover"
                    content={
                        <img style={{ objectFit: 'cover', imageRendering: '-webkit-optimize-contrast', cursor: 'zoom-in', width: `${type === 'pc' ? 384 : 187}px`, height: `${type === 'pc' ? 216 : 333}px` }} className={type} data-url={moreUrl} alt={name} src={img} onClick={this.jumpToMore}/>
                    }>
                    <div className={`preWp ${isActive ? 'active' : ''}`} onClick={this.handleClick}>
                        <span className="name">{name}</span>
                        <span className="desc">{desc}</span>
                    </div>
                </Popover>
            </StyledPreComponent>
        )
    }
}

export default PreComponent
